import { FILTER_CREATED, GET_GAME, GET_SEARCH, ORDER_NAME, ORDER_RATING } from "../actions types/actionTypes"

const initialState = {
   videogame : [],
   auxGame:[]
}


function rootReducer(state = initialState, action) {
    switch (action.type) {
        case GET_GAME:
          console.log(action.payload)
            return {
                ...state,
                videogame: action.payload,
                auxGame: action.payload
            };
            case FILTER_CREATED:
                const filterCreated = state.auxGame
                const filter = action.payload === 'created'?
                 filterCreated.filter(e=> e.createdInDb): 
                 filterCreated.filter(e=> !e.createdInDb)
              return{
                  ...state,
                  videogame: action.payload ==='All'? filterCreated: filter
              };
              case ORDER_NAME:
                const orderName = state.auxGame
                     const orderNameEnd = action.payload === 'asc'?  orderName.sort((a,b)=>{
                        if (a.name > b.name) return 1;
                        if (a.name < b.name) return -1;
                        return 0;                 
                  }):  orderName.sort((a,b)=>{
                    if (a.name > b.name) return -1;
                    if (a.name < b.name) return 1;
                    return 0;
                  })
                  console.log(orderNameEnd)
                  return{
                      ...state,
                      videogame: action.payload === 'n'? orderName: orderNameEnd
                  };
                 
                  case ORDER_RATING:                   
                    const orderRatingEnd = action.payload === 'mejor'? state.auxGame.sort((a,b)=>{
                        if(a.rating>b.rating)return 1
                        if (a.rating<b.rating)return -1
                        return 0                      
                    }): state.auxGame.sort((a,b)=>{
                        if(b.rating>a.rating)return 1
                        if(b.rating<a.rating)return -1
                        return 0
                    })
                  return{
                      ...state,
                      videogame: orderRatingEnd
                  }

                  case GET_SEARCH:
                      return{
                          ...state,
                          videogame: action.payload
                      }
        default:
            return state
    }
}


export default rootReducer